/*------------------------------------*\
    #BODY
\*------------------------------------*/

/**
 * Body base styles
 */
body {
	background: $color-white;
	color: $color-gray-50;
	font: 100%/1.5 $font;
	-webkit-text-size-adjust: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	font-family: "Avenir_roman",serif;

}

@font-face{

font-family: "euromode_regular";
src: url("../fonts/Euromoderegular.bmap") format("bitmap");
}

@font-face{

font-family: "euromode_bold";
src: url("../fonts/Euromodebold.bmap") format("bitmap");
}

@font-face{

font-family: "Avenir_roman";
src: url("../fonts/Avenir-Roman-12.ttf") format("truetype");
}

@font-face{

font-family: "Avenir_light";
src: url("../fonts/Avenir-Light-07.ttf") format("truetype");
}

@font-face{

font-family: "Avenir_bold";
src: url("../fonts/Avenir-Heavy-05.ttf") format("truetype");
}
