.article-type {
  width: 730px;
  height: 45px;
  padding-left: 23px;
  padding-right: 21px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-weight: bold;
}

.article-type .pin__blanc {
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    display: block;
    height: 25px;
    width: 17px
  }
}

.article-type__event {
  background-color: #DE9100;
}

.article-type__logiciel {
  background-color: #9B9B9B;
}

.article-type__codage {
  background-color: #7E1212;
}

.article-type__focus {
  background-color: #694687;
}

.article-type__veilles {
  background-color: #50E3C2;
}

.article-type__lectures {
  background-color: #579C08;
}

.article-type__histoire_du_web {
  background-color: #154069;
}
