/*------------------------------------*\
    #MAIN
\*------------------------------------*/

/**
 * Main content area
 */
[role=main] {
	flex: 1;
}

.menu_link-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.menu_link {
	font-family: "Avenir_bold";
	font-size: 17px;
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	background-color: #282828;
	width: 211px;
	padding : 4px 0 4px 20px;
	margin-bottom: 2px;
}

.menu_link-home {
	margin-bottom: 15px;
}

.menu_link-forum {
	margin-top: 15px;
}

.menu-link-active {
	color: #383838;
	background-color: #D8D8D8

}

.menu_colonne{
	width: 211px;
	height: 100vh;
	background-color: #383838;
}

.menu_colonne .logoECV {
	width: 186px;
	height: auto;
	margin-left: auto;
	margin-right: auto;
	margin-top: 31px;
	margin-bottom: 34px;
	display: block;
}
